import { Component } from "react";
import '../src/css/item.css';
import axios from 'axios';

class Sub extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }

    componentDidMount() {
        this._getData();
    }
    
    _getData = async () => {
        const current = decodeURI(window.location.href);
        const url = current.split('/').reverse()[0]
        const body = { id : url };

        const res = await axios('/find/item', {
            method: 'POST',
            data : {'where': body},
            header: new Headers()

        });

        this.setState(res.data[0]);
    }
    
    render() {

        return(
            <main id="sub">
                <section className="topBanner flex">
                    <div>
                        <h4>{this.state.subTitle1}</h4>
                        <h2>{this.state.title}</h2>
                        <h3>{this.state.subTitle2}</h3>
                        <input type="button" value="상담신청" />
                    </div>
                </section>
                <section id='contents' className="container">
                    <div id="sInfo">
                        <h4>요약정보</h4>
                        <div className="flex">
                            <div>
                                <img src="../src/img/icons/icon02.png" alt="대출대상 아이콘" />
                                <p className="title">대출대상</p>
                                <p className="desc">{this.state.sInfo1}</p>
                            </div>
                            <div>
                                <img src="../src/img/icons/icon06.png" alt="대출한도 아이콘" />
                                <p className="title">대출한도</p>
                                <p className="desc">{this.state.sInfo2}</p>
                            </div>
                            <div>
                                <img src="../src/img/icons/icon07.png" alt="대출금리 아이콘" />
                                <p className="title">대출금리</p>
                                <p className="desc">{this.state.sInfo3}</p>
                            </div>
                        </div>
                    </div>
                    <div id="dInfo">
                        <h4>상품정보</h4>
                        <table>
                            <tr>
                                <th>대출대상</th>
                                <td>{this.state.dInfo1}</td>
                            </tr>
                            <tr>
                                <th>대출한도</th>
                                <td>{this.state.dInfo2}</td>
                            </tr>
                            <tr>
                                <th>정상금리</th>
                                <td>{this.state.dInfo3}</td>
                            </tr>
                            <tr>
                                <th>연체금리</th>
                                <td>{this.state.dInfo4}</td>
                            </tr>
                            <tr>
                                <th>계약기간</th>
                                <td>{this.state.dInfo5}</td>
                            </tr>
                            <tr>
                                <th>대출대상</th>
                                <td>{this.state.dInfo6}</td>
                            </tr>
                            <tr>
                                <th>상환방식</th>
                                <td>{this.state.dInfo7}</td>
                            </tr>
                        </table>
                    </div>
                </section>
            </main>
        )
    }
}

export default Sub;