import React, { Component } from 'react';
import '../src/css/list.css';

import axios from 'axios';
import Modify from './Modify';

class ItemList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            modalOn: false,
            list : [],
            update : false,
        }
    }

    componentDidMount() {
        this._getData();
    }

    _getData = async () => {
        const res = await axios.get('/get/data/item');
        if(res.data[0] === undefined) {
            let cover = [];
            cover.push(res.data);
        
            return this.setState({ list : cover })
        }
        this.setState({ list : res.data });
    }

    delete = async (el) => {
        const remove = window.confirm(el.title + '을 삭제합니까?');
    
        if(remove) {
            const body = { id : el.id }
            const res = await axios('/delete/data', {
                method : 'POST',
                data : { 'delete' : body },
                headers: new Headers()
            })
            
            if(res.data) {
                alert('데이터를 삭제했습니다.')
                return window.location.reload();
            }
        }
    }

    openModal = key => {
        this.setState({
            modalOn: !this.state.modal,
            modify: this.state.list[key]
        });

    }

    
    
    render() {
        const { list } = this.state;
        var _modal = null;
        if(this.state.modalOn === false) {
            _modal = null;
        } else {
            _modal = <Modify modify={this.state.modify}  onClose={
                function(e) {
                    alert('수정을 취소했습니다.');
                    window.location.reload()
                }
            }/>
        }

        return(
            <section className="list">
                <table>
                    <thead>
                        <th>No.</th>
                        <th>상품명</th>
                        <th>대출대상</th>
                        <th>대출한도</th>
                        <th>계약기간</th>
                        <th>상환방식</th>
                        <th>수수료</th>
                        <th>수정</th>
                        <th>삭제</th>
                    </thead>
                    <tbody>
                    {list.length !== 0
                        ? list.map( (el, key) => {
                            return(
                                <tr key={key}>
                                    <td>{key + 1}</td>
                                    <td>{el.title}</td>
                                    <td>{el.sInfo1}</td>
                                    <td>{el.sInfo2}</td>
                                    <td>{el.sInfo3}</td>
                                    <td>{el.dInfo6}</td>
                                    <td>{el.dInfo7}</td>
                                    <td><input type="button" value="수정" onClick={() => this.openModal(key)}/></td>
                                    <td><input type="button" value="삭제" onClick={() => this.delete(el)}/></td>
                                </tr>
                            )
                        })
                        : null
                    }
                    </tbody>
                </table>
                {_modal}
            </section>
        )
    }
}

export default ItemList;