import React, { Component } from 'react';
import '../src/css/create.css';
import axios from 'axios';

class CreatItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }

    onChange = e => {
        this.setState({
            [e.target.name] : e.target.value
        });
    }
    

    addData = async(e) => {
        const add = this.state;
        e.preventDefault();

        if(add !== null) {
            const item = {
                title: add.title,
                subTitle1: add.subTitle1,
                subTitle2: add.subTitle2,
                sInfo1: add.sInfo1,
                sInfo2: add.sInfo2,
                sInfo3: add.sInfo3,
                dInfo1: add.dInfo1,
                dInfo2: add.dInfo2,
                dInfo3: add.dInfo3,
                dInfo4: add.dInfo4,
                dInfo5: add.dInfo5,
                dInfo6: add.dInfo6,
                dInfo7: add.dInfo7
            }

            if(!e.target.title.value || 
                !e.target.subTitle1.value || 
                !e.target.subTitle2.value || 
                !e.target.sInfo1.value || 
                !e.target.sInfo2.value || 
                !e.target.sInfo3.value ||
                !e.target.dInfo1.value ||
                !e.target.dInfo2.value ||
                !e.target.dInfo3.value ||
                !e.target.dInfo4.value ||
                !e.target.dInfo5.value ||
                !e.target.dInfo6.value ||
                !e.target.dInfo7.value
                ) {
                alert("모두 입력해주세요.")
            }

            const res = await axios('/add/data', {
                method: 'POST',
                data : item,
                headers: new Headers()
            });

            if(res.data) {
                alert("상품을 추가.");
                return window.location.reload();

            }
        }
    }

    render() {
        return(
            <div className="modal">
                <div id="create">
                    <h2>상품추가</h2>
                    <form method='POST' onSubmit={this.addData}>
                        <div className="f_flex">
                            <label htmlFor="title">상품명</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="title" id="title" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="subTitle1">배너 상단 설명1</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="subTitle1" id="subTitle1" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="subTitle2">배너 하단 설명2</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="subTitle2" id="subTitle2" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="sInfo1">대출대상 요약</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="sInfo1" id="sInfo1" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="sInfo2">대출한도 요약</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="sInfo2" id="sInfo2" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="sInfo3">대출금리 요약</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="sInfo3" id="sInfo3" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo1">대출대상</label>
                            <textarea onChange={this.onChange} type="text" autocomplete='off' name="dInfo1" id="dInfo1" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo2">대출한도</label>
                            <textarea onChange={this.onChange} type="text" autocomplete='off' name="dInfo2" id="dInfo2" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo3">정상금리</label>
                            <textarea onChange={this.onChange} type="text" autocomplete='off' name="dInfo3" id="dInfo3" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo4">연체금리</label>
                            <textarea onChange={this.onChange} type="text" autocomplete='off' name="dInfo4" id="dInfo4" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo5">계약관리</label>
                            <textarea onChange={this.onChange} type="text" autocomplete='off' name="dInfo5" id="dInfo5" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo6">상환방식</label>
                            <input onChange={this.onChange} type="text" autocomplete='off' name="dInfo6" id="dInfo6" value={this.state.name}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo7">수수료</label>
                            <textarea onChange={this.onChange} type="text" autocomplete='off' name="dInfo7" id="dInfo7" value={this.state.name}/>
                        </div>
                        <div className="flex">
                            <input type="submit" value="추가하기" />
                            <input type="button" value="취소" onClick={this.props.onClose}/>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default CreatItem;