import React, { Component } from 'react';
import '../src/css/create.css';
import axios from 'axios';

class Modify extends Component {
    constructor(props) {
        super(props)
        this.state = {
            id: this.props.modify.id,
            title: this.props.modify.title,
            subTitle1: this.props.modify.subTitle1,
            subTitle2: this.props.modify.subTitle2,
            sInfo1: this.props.modify.sInfo1,
            sInfo2: this.props.modify.sInfo2,
            sInfo3: this.props.modify.sInfo3,
            dInfo1: this.props.modify.dInfo1,
            dInfo2: this.props.modify.dInfo2,
            dInfo3: this.props.modify.dInfo3,
            dInfo4: this.props.modify.dInfo4,
            dInfo5: this.props.modify.dInfo5,
            dInfo6: this.props.modify.dInfo6,
            dInfo7: this.props.modify.dInfo7
        }
    }

    modifyData = async(e) => {
        const modify = this.state;
        e.preventDefault();

        if(modify !== null) {
            const item = {
                id: modify.id,
                title: modify.title,
                subTitle1: modify.subTitle1,
                subTitle2: modify.subTitle2,
                sInfo1: modify.sInfo1,
                sInfo2: modify.sInfo2,
                sInfo3: modify.sInfo3,
                dInfo1: modify.dInfo1,
                dInfo2: modify.dInfo2,
                dInfo3: modify.dInfo3,
                dInfo4: modify.dInfo4,
                dInfo5: modify.dInfo5,
                dInfo6: modify.dInfo6,
                dInfo7: modify.dInfo7
            }

            const res = await axios('/modify/data', {
                method: 'POST',
                data : item,
                headers: new Headers()
            });

            if(res.data) {
                alert('상품 정보가 수정되었스니다.');
                return window.location.reload();
            } else {
                alert('왜 안 되고 지랄이야')
            }
        }
    }

    render() {

        return (
            <div className='modal'>
                <div id="modify">
                    <h2>상품 수정</h2>
                    <form  method='post' onSubmit={this.modifyData}>
                        <div className="f_flex">
                            <label htmlFor="title">상품명</label>
                            <input onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="title" id="title" autocomplete='off' value={this.state.title}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="subTitle1">배너 상단 설몀1</label>
                            <input onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="subTitle1" id="subTitle1" autocomplete='off' value={this.state.subTitle1}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="subTitle2">배너 하단 설몀2</label>
                            <input onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="subTitle2" id="subTitle2" autocomplete='off' value={this.state.subTitle2}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="sInfo1">대출대상 요약</label>
                            <input onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="sInfo1" id="sInfo1" autocomplete='off' value={this.state.sInfo1}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="sInfo2">대출한도 요약</label>
                            <input onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="sInfo2" id="sInfo2" autocomplete='off' value={this.state.sInfo2}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="sInfo3">대출금리 요약</label>
                            <input onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="sInfo3" id="sInfo3" autocomplete='off' value={this.state.sInfo3}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo1">대출대상</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo1" id="dInfo1" autocomplete='off' value={this.state.dInfo1}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo2">대출한도</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo2" id="dInfo2" autocomplete='off' value={this.state.dInfo2}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo3">정상금리</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo3" id="dInfo3" autocomplete='off' value={this.state.dInfo3}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo4">연체금리</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo4" id="dInfo4" autocomplete='off' value={this.state.dInfo4}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo5">계약관리</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo5" id="dInfo5" autocomplete='off' value={this.state.dInfo5}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo6">상환방식</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo6" id="dInfo6" autocomplete='off' value={this.state.dInfo6}/>
                        </div>
                        <div className="f_flex">
                            <label htmlFor="dInfo7">수수료</label>
                            <textarea onChange={function(e){this.setState({[e.target.name]: e.target.value})}.bind(this)} type="text" name="dInfo7" id="dInfo7" autocomplete='off' value={this.state.dInfo7}/>
                        </div>
                    <div className="flex">
                        <input type="submit" value="수정완료" />
                        <input type="button" id='false' value="취소" onClick={this.props.onClose} />
                    </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default Modify;