import React, { Component } from 'react';
import {
  BrowserRouter,
  Routes,
  Route
} from "react-router-dom";
import Client from "../client/index";
import Main from '../client/pages/Main';
import Sub from "../client/pages/Sub";
import Admin from "../admin/index";
import Item from "../admin/pages/Item";
import Request from "../admin/pages/Request"
import axios from 'axios';

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      host : '',
    }
  }

  componentDidMount() {
    this._getHost();
  }

  _getHost = async() => {
    const res = await axios.get('/api/host');
    this.setState({ host : res.data.host })
  }
  
  render() {
    return (
      <BrowserRouter>
      <Routes>
        <Route path="/test" element={<Client />}>
          <Route path='/test' element={<Main/>} />
          <Route path='/test/sub/:key' element={<Sub/>} />
        </Route>
        <Route path="/test/admin" element={<Admin />}>
          <Route path='/test/admin/item' element={<Item/>} />
          <Route path='/test/admin/request' element={<Request/>} />
        </Route>
      </Routes>
    </BrowserRouter>
    );
  }
}

export default App;