import React, { Component } from 'react';
import ItemList from '../components/ItemList';
import CreateItem from '../components/CreateItem';

class Item extends Component {
    constructor(props) {
        super(props)
        this.state = {
            modalOn: false,

        }
    }

    render() {
        const { list } = this.state;
        var _modal = null;
        if(this.state.modalOn === false) {
            _modal = null;
        } else {
            _modal = <CreateItem modify={this.state.modify}  onClose={
                function(e) {
                    alert('상품추가 취소.');
                    window.location.reload()
                }
            }/>
        }

        return (
            <div id="item">
                <h2>대출 상품 리스트</h2>
                <input type="button" value="상품 추가"
                    onClick={function(){
                        this.setState({
                            modalOn: !this.state.modal,
                        })
                }.bind(this)} />
                {_modal}
                <ItemList/>
            </div>
        );
    }
}

export default Item;